import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-in-progress-screen',
  templateUrl: './in-progress-screen.component.html',
  styleUrls: ['./in-progress-screen.component.scss']
})
export class InProgressScreenComponent implements OnInit {

  constructor() { }

  @Input() players: any[];
  @Input() playersSubmitted: string[];
  @Input() timer?: number;
  @Input() instructions?: string;


  ngOnInit(): void {
  }

  removeClass(element: string, className: string) {
    const node = document.querySelector(element);
    node?.classList.remove(className);
  }

}
