import { AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';

interface IPlayer {
  name: string;
  profile: string;
  color: string;
}

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.scss']
})

export class PostComponent implements AfterViewInit, OnInit {

  // take in username, player icon, post image, post text, post type
  // eventually take in star rating/video duration as optional attributes

  @Input() submitter: IPlayer;
  @Input() sacrifice?: IPlayer;
  @Input() votes?: string[];
  @Input() image: string;
  @Input() caption: any;
  @Input() type: string;
  @Input() small?: boolean;
  @Input() showVotes?: boolean = false;
  @Input() animate?: boolean = false;
  @Input() maxVotes: number = 0;
  @Input() voting?: boolean;
  @Input() sound = false;
  @Input() medal?: string;

  triggerText = false;
  triggerExit = false;

  triggerPoints = false;
  exitPoints = false;

  voteTimer: any;
  postDisplayDuration = 12000;
  textDisplayDelay = 2000;
  voteRevealDuration = 500;
  animationSequence: boolean[] = [];
  voteIndex: number;

  numVotes: number;
  voteTotal = 0;

  goldPoints = 100;
  silverPoints = 50;
  bronzePoints = 25;

  clientWidth: number;

  @ViewChild('postContainerSmall') postContainerSmall: ElementRef;
  @ViewChild('postTextSmall') postTextSmall: ElementRef;

  @ViewChild('postContainerLarge') postContainerLarge: ElementRef;
  @ViewChild('postTextLarge') postTextLarge: ElementRef;

  constructor() {
  }

  // resize text to fit in certain number of lines
  resizeToFit(container: HTMLElement, textElement: HTMLElement, fontSize: number, targetLines: number) {
    let width = container.offsetWidth;
    if (width === 0) {
      width = this.small ? 160 : 450;
    }
    let divHeight = container.offsetHeight;
    if(divHeight === 0) {
      divHeight = this.small ? 67 : 168;
    }
    const lineHeight = parseInt(window.getComputedStyle(textElement, null).getPropertyValue('line-height'));
    if(lineHeight < 1) {
      return;
    }
    const lines = Math.floor(divHeight / lineHeight);
    if (lines > targetLines){
      textElement.style.fontSize = (fontSize - 1) + 'px';
      this.resizeToFit(container, textElement, fontSize - 1, targetLines);
    }
  }

  playSFX(file: string, volume = 0.5) {
    const sfx = new Audio(file);
    sfx.volume = volume;
    sfx.play();
  }

  padLeadingZeros(num: Number, size: Number) {
      var s = num + "";
      while (s.length < size) s = "0" + s;
      return s;
  }

  doResize() {
    if (this.postContainerSmall != null && this.postTextSmall != null) {
      const titleContainerSmall = this.postContainerSmall.nativeElement as HTMLElement;
      const titleTextSmall = this.postTextSmall.nativeElement as HTMLElement;
      const titleSizeSmall = parseInt(window.getComputedStyle(titleTextSmall, null).getPropertyValue('font-size'));
      this.resizeToFit(titleContainerSmall, titleTextSmall, titleSizeSmall, 2);
    }
    if (this.postContainerLarge != null && this.postTextLarge != null) {
      const titleContainerLarge = this.postContainerLarge.nativeElement as HTMLElement;
      const titleTextLarge = this.postTextLarge.nativeElement as HTMLElement;
      const titleSizeLarge = parseInt(window.getComputedStyle(titleTextLarge, null).getPropertyValue('font-size'));
      this.resizeToFit(titleTextLarge, titleTextLarge, titleSizeLarge, 3);
    }
  }

  ngAfterViewInit(): void {
      if (this.animate === false || this.showVotes === true) {
        this.doResize();
      }
  }

  doExit() {
    setTimeout(() => {
      this.triggerPoints = true;
      if(this.sound) {
        this.playSFX('assets/audio/sfx/cash.mp3');
      }
    }, 1000);
    setTimeout(() => {
      this.exitPoints = true;
    }, 2500);
  }

  ngOnInit(): void {
    if(this.votes != null) {
      this.numVotes = this.votes.length;
      const bronzeVotes = this.votes.filter((vote: any) => vote === 'bronze').length;
      const silverVotes = this.votes.filter((vote: any) => vote === 'silver').length;
      const goldVotes = this.votes.filter((vote: any) => vote === 'gold').length;
      this.voteTotal += bronzeVotes * this.bronzePoints;
      this.voteTotal += silverVotes * this.silverPoints;
      this.voteTotal += goldVotes * this.goldPoints;

      for(let i = 0; i < this.maxVotes; i++) {
        this.animationSequence.push(false);
      }
    } else {
      this.numVotes = 0;
    }
    if(this.animate) {
      if(this.votes == null) {
        setTimeout(() => {
          if(!this.voting) {
            this.playSFX('assets/audio/sfx/woosh.wav', 0.4);
          }
          this.triggerText = true;
          setTimeout(()=> {
            this.doResize();
          }, 0);
          
        }, this.textDisplayDelay);
        setTimeout(() => {
          this.triggerExit = true;
        }, this.postDisplayDuration);
      } else {

        setTimeout(() => {
          if(this.votes != null) {
            this.voteIndex = this.numVotes - 1;
            this.voteTimer = setInterval(() => {
              if (this.voteIndex < 0) {
                clearInterval(this.voteTimer);
                setTimeout(()=> {
                  this.doExit();
                }, this.voteRevealDuration * (this.maxVotes - this.numVotes));


              } else {
                if(this.animationSequence[this.voteIndex] != null) {
                  if (this.sound) {
                    const pointIndex = this.maxVotes - this.voteIndex;
                    this.playSFX('assets/audio/sfx/point' + pointIndex + '.wav')
                  }
                  this.animationSequence[this.voteIndex] = true;
                }
                this.voteIndex--;
              }


            }, this.voteRevealDuration)
          }
        }, 1000);
      }
    }
  }

}
